	module.exports = {"data":{"markdownRemark":{"html":"<!---\nThis is an autogenerated file!\n\nPlease do not edit this file directly, but instead make changes to the\nsigs.yaml file in the project root.\n\nTo understand how this file is generated, see https://git.k8s.io/community/generator/README.md\n-->\n<h1>AWS Special Interest Group</h1>\n<p>Covers maintaining, supporting, and using Kubernetes hosted on AWS Cloud.</p>\n<h2>Meetings</h2>\n<ul>\n<li>\n<p>Regular SIG Meeting: <a href=\"https://zoom.us/my/k8ssigaws\">Fridays at 9:00 PT (Pacific Time)</a> (biweekly). <a href=\"http://www.thetimezoneconverter.com/?t=9:00&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/1-i0xQidlXnFEP9fXHWkBxqySkXwJnrGJP9OGyP2_P14/edit\">Meeting notes and Agenda</a>.</li>\n</ul>\n</li>\n</ul>\n<h2>Leadership</h2>\n<h3>Chairs</h3>\n<p>The Chairs of the SIG run operations and processes governing the SIG.</p>\n<ul>\n<li>Justin Santa Barbara (<strong><a href=\"https://github.com/justinsb\">@justinsb</a></strong>)</li>\n<li>Kris Nova (<strong><a href=\"https://github.com/kris-nova\">@kris-nova</a></strong>), Heptio</li>\n<li>Bob Wise (<strong><a href=\"https://github.com/countspongebob\">@countspongebob</a></strong>), AWS</li>\n</ul>\n<h2>Contact</h2>\n<ul>\n<li><a href=\"https://kubernetes.slack.com/messages/sig-aws\">Slack</a></li>\n<li><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-aws\">Mailing list</a></li>\n<li><a href=\"https://github.com/kubernetes/community/labels/sig%2Faws\">Open Community Issues/PRs</a></li>\n</ul>\n<h2>Subprojects</h2>\n<p>The following subprojects are owned by sig-aws:</p>\n<ul>\n<li>\n<p><strong>cloud-provider-aws</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/cloud-provider-aws/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/cloud-provider-aws/master/OWNERS</a></li>\n</ul>\n</li>\n</ul>\n<h2>GitHub Teams</h2>\n<p>The below teams can be mentioned on issues and PRs in order to get attention from the right people.\nNote that the links to display team membership will only work if you are a member of the org.</p>\n<p>The google groups contain the archive of Github team notifications.\nMentioning a team on Github will CC its group.\nMonitor these for Github activity if you are not a member of the team.</p>\n<table>\n<thead>\n<tr>\n<th>Team Name</th>\n<th align=\"center\">Details</th>\n<th align=\"center\">Google Groups</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@kubernetes/sig-aws-misc</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-aws-misc\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-aws-misc\">link</a></td>\n<td>General Discussion</td>\n</tr>\n</tbody>\n</table>\n<!-- BEGIN CUSTOM CONTENT -->\n<h2>Participate</h2>\n<p>For information on how to participate in this community, read our <a href=\"CONTRIBUTING.md\">contribution guidelines</a>.</p>\n<!-- END CUSTOM CONTENT -->"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-aws/"}}

